/* eslint-disable react/destructuring-assignment */
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Select from 'react-select';
import PenSize from './PenSize/PenSize';
import Tool from './Tool/Tool';
import SwitchColor from './SwitchColor/SwitchColor';

import classes from './Tools.module.scss';

class Tools extends Component {
    toolsList = [
      {
        id: 'pen',
        iconName: 'pen-fancy',
        tooltip: 'Pen (P)',
        keyMap: {
          PEN: 'p',
        },
      },
      {
        id: 'eraser',
        iconName: 'eraser',
        tooltip: 'Eraser (E)',
        keyCode: 'KeyE',
      },
      {
        id: 'stroke',
        iconName: 'pencil-ruler',
        tooltip: 'Stroke (S)',
        keyCode: 'KeyS',
      },
      {
        id: 'bucket',
        iconName: 'fill-drip',
        tooltip: 'Bucket (B)',
        keyCode: 'KeyB',
      },
      {
        id: 'bucketall',
        iconName: 'palette',
        tooltip: 'Bucket all pixels at the same color (A)',
        keyCode: 'KeyA',
      },
    ];

  options = [
    { value: 32, label: '32' },
    { value: 64, label: '64' },
    { value: 128, label: '128' },
  ];

  setActiveTool = (e) => {
    const { id } = e.currentTarget;
    if (id !== this.props.activeTool) {
      this.props.changeState('activeTool', id);
      localStorage.setItem('activeTool', id);
    }
  }

  changePencilSizeHandler = (e) => {
    const { id } = e.currentTarget;
    switch (id) {
      case 'firstSize':
        this.props.changeState('pencilSize', 1);
        this.props.changeState('activePenSize', 'firstSize');
        localStorage.setItem('pencilSize', 1);
        localStorage.setItem('activePenSize', 'firstSize');
        break;
      case 'secondSize':
        this.props.changeState('pencilSize', 2);
        this.props.changeState('activePenSize', 'secondSize');
        localStorage.setItem('pencilSize', 2);
        localStorage.setItem('activePenSize', 'secondSize');
        break;
      case 'thirdSize':
        this.props.changeState('pencilSize', 3);
        this.props.changeState('activePenSize', 'thirdSize');
        localStorage.setItem('pencilSize', 3);
        localStorage.setItem('activePenSize', 'thirdSize');
        break;
      case 'forthSize':
        this.props.changeState('pencilSize', 4);
        this.props.changeState('activePenSize', 'forthSize');
        localStorage.setItem('pencilSize', 4);
        localStorage.setItem('activePenSize', 'forthSize');
        break;
      default: break;
    }
  }

  changeCanvasSizeHandler = (data) => {
    this.props.changeState('pixelsNumber', data.value);
    localStorage.setItem('pixelsNumber', data.value);
  }

  render() {
    const {
      changeState, activeTool, activePenSize, currentColor, pixelsNumber,
    } = this.props;

    return (
      <>
        <section className={classes.ToolsSection}>
          <PenSize
            activeId={activePenSize}
            changePencilSizeHandler={this.changePencilSizeHandler}
          />
          <Select
            onChange={this.changeCanvasSizeHandler}
            options={this.options}
            placeholder={pixelsNumber}
            className={classes.SelectInput}
          />
          <div className={classes.MainTools}>
            {this.toolsList.map((tool) => (
              <Tool
                id={tool.id}
                key={tool.id}
                active={tool.id === activeTool}
                icon={tool.iconName}
                setActiveTool={this.setActiveTool}
                tooltip={tool.tooltip}
              />
            ))}
          </div>
          <SwitchColor
            changeState={changeState}
            currentColor={currentColor}
          />
        </section>
      </>
    );
  }
}

Tools.propTypes = {
  pencilSize: PropTypes.number.isRequired,
  pixelsNumber: PropTypes.number.isRequired,
  currentColor: PropTypes.string.isRequired,
  activePenSize: PropTypes.string.isRequired,
  activeTool: PropTypes.string.isRequired,
};

export default Tools;
