import React, { Component } from 'react';
import PropTypes from 'prop-types';

import GIF from 'gif.js';
import downloadjs from 'downloadjs';
import SaveGif from './SaveGif/SaveGif';
import SavePng from './SavePng/SavePng';

class SettingsTools extends Component {
  componentDidUpdate() {
    const { isNeedToSaveAsGif, isNeedToSaveAsPng } = this.props;
    if (isNeedToSaveAsGif) {
      this.saveGifHandler();
    }
    if (isNeedToSaveAsPng) {
      this.savePngHandler();
    }
  }

    saveGifHandler = () => {
      const { framesList, changeState } = this.props;
      const gif = new GIF({
        workers: 2,
        quality: 10,
        workerScript: './gif.worker.js',
      });
      framesList.forEach((frame) => {
        gif.addFrame(frame.imageData);
      });
      gif.on('finished', (blob) => {
        downloadjs(blob, 'exportedGif.gif', 'image/gif');
      });
      gif.render();
      changeState('isNeedToSaveAsGif', false);
    }

    savePngHandler = () => {
      const { changeState } = this.props;
      const canvas = document.getElementById('canvas');
      canvas.toBlob((blob) => {
        downloadjs(blob, 'canvasImage.png');
      });
      changeState('isNeedToSaveAsPng', false);
    }

    render() {
      return (
        <div>
          <SaveGif
            saveGifHandler={this.saveGifHandler}
          />
          <SavePng
            savePngHandler={this.savePngHandler}
          />
        </div>
      );
    }
}

SettingsTools.propTypes = {
  framesList: PropTypes.arrayOf(PropTypes.object).isRequired,
};

export default SettingsTools;
