import { HotKeys } from 'react-hotkeys';
import React, { Component } from 'react';
import ReactTooltip from 'react-tooltip';
import { keyMap } from './constants/constants';
import './static/icons';
import classes from './App.module.scss';

import Header from './components/Header/Header';
import Tools from './components/Tools/Tools';
import Frames from './components/Frames/Frames';
import Canvas from './components/Canvas/Canvas';
import AnimationPlayer from './components/AnimationPlayer/AnimationPlayer';

class App extends Component {
  state = {
    activeTool: localStorage.getItem('activeTool') ? localStorage.getItem('activeTool') : 'pen',
    activePenSize: localStorage.getItem('activePenSize') ? localStorage.getItem('activePenSize') : 'firstSize',
    currentColor: localStorage.getItem('currentColor') ? localStorage.getItem('currentColor') : '#000000',
    activeFrame: 0,
    framesList: [
      {},
    ],
    pixelsNumber: localStorage.getItem('pixelsNumber') ? localStorage.getItem('pixelsNumber') : 32,
    pencilSize: localStorage.getItem('pencilSize') ? localStorage.getItem('pencilSize') : 1,
    isSignedIn: false,
    isNeedToAddNewFrame: false,
    isNeedToDeleteFrame: false,
    isNeedToCloneFrame: false,
    isNeedToSaveAsGif: false,
    isNeedToSaveAsPng: false,
  };

  handlers = {
    PEN: () => this.shortcutsHandler('activeTool', 'pen'),
    BUCKET: () => this.shortcutsHandler('activeTool', 'bucket'),
    STROKE: () => this.shortcutsHandler('activeTool', 'stroke'),
    BUCKETALL: () => this.shortcutsHandler('activeTool', 'bucketall'),
    ERASER: () => this.shortcutsHandler('activeTool', 'eraser'),
    ADDNEWFRAME: () => this.shortcutsHandler('isNeedToAddNewFrame', true),
    DELETEFRAME: () => this.shortcutsHandler('isNeedToDeleteFrame', true),
    CLONEFRAME: () => this.shortcutsHandler('isNeedToCloneFrame', true),
    SAVEASGIF: () => this.shortcutsHandler('isNeedToSaveAsGif', true),
    SAVEASPNG: () => this.shortcutsHandler('isNeedToSaveAsPng', true),
    FIRSTPENSIZE: () => { this.shortcutsHandler('pencilSize', 1); this.shortcutsHandler('activePenSize', 'firstSize'); },
    SECONDPENSIZE: () => { this.shortcutsHandler('pencilSize', 2); this.shortcutsHandler('activePenSize', 'secondSize'); },
    THIRDPENSIZE: () => { this.shortcutsHandler('pencilSize', 3); this.shortcutsHandler('activePenSize', 'thirdSize'); },
    FORTHPENSIZE: () => { this.shortcutsHandler('pencilSize', 4); this.shortcutsHandler('activePenSize', 'forthSize'); },
  }

  shortcutsHandler = (prop, value) => {
    this.setState({ [prop]: value });
    localStorage.setItem(prop, value);
  }

  changeState = (property, value) => {
    this.setState({ [property]: value });
  }

  convertImageToFrame = () => {
    const { activeFrame, framesList } = this.state;
    const newFramesList = [...framesList];
    const newActiveFrame = newFramesList.find((frame, i) => i === activeFrame);
    const canvas = document.querySelector('#canvas');
    const mainCtx = canvas.getContext('2d');
    const imageData = mainCtx.getImageData(0, 0, canvas.width, canvas.height);
    newActiveFrame.imageData = imageData;
    this.setState({ framesList: newFramesList });
  }

  changeFrames = (id1, id2) => {
    const { framesList } = this.state;
    const newFramesList = [...framesList];
    [newFramesList[id1], newFramesList[id2]] = [newFramesList[id2], newFramesList[id1]];
    this.setState({ framesList: newFramesList });
  }

  render() {
    const {
      activeTool,
      activePenSize,
      currentColor,
      isSignedIn,
      pixelsNumber,
      activeFrame,
      framesList,
      pencilSize,
      isNeedToAddNewFrame,
      isNeedToDeleteFrame,
      isNeedToCloneFrame,
      isNeedToSaveAsGif,
      isNeedToSaveAsPng,
    } = this.state;
    return (
      <>
        <HotKeys keyMap={keyMap} handlers={this.handlers}>
          <Header
            isSignedIn={isSignedIn}
            changeState={this.changeState}
          />
          <div className={classes.App}>
            <ReactTooltip place="right" type="dark" effect="float" />
            <Tools
              changeState={this.changeState}
              activeTool={activeTool}
              activePenSize={activePenSize}
              currentColor={currentColor}
              pixelsNumber={pixelsNumber}
              getChildContext={this.getChildContext}
            />
            <Frames
              activeFrame={activeFrame}
              changeState={this.changeState}
              framesList={framesList}
              changeFrames={this.changeFrames}
              isNeedToAddNewFrame={isNeedToAddNewFrame}
              isNeedToDeleteFrame={isNeedToDeleteFrame}
              isNeedToCloneFrame={isNeedToCloneFrame}

            />
            <Canvas
              activeTool={activeTool}
              currentColor={currentColor}
              convertImageToFrame={this.convertImageToFrame}
              pencilSize={pencilSize}
              pixelsNumber={pixelsNumber}
            />
            <AnimationPlayer
              framesList={framesList}
              isNeedToSaveAsGif={isNeedToSaveAsGif}
              isNeedToSaveAsPng={isNeedToSaveAsPng}
              changeState={this.changeState}
            />
          </div>
        </HotKeys>
      </>
    );
  }
}

export default App;
